import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
import { 
  Camera, 
  MapPin, 
  Clock, 
  X, 
  Upload,
  ArrowLeft,
  AlertCircle
} from 'lucide-react';
import { inspectionsApi } from '../services/api';
import toast from 'react-hot-toast';
import './MobileInspection.css';

interface Photo {
  id?: number;
  file: File;
  preview: string;
  latitude?: number;
  longitude?: number;
  timestamp: string;
  objectId: number;
}

interface InspectionObject {
  id: number;
  vin?: string;
  registration_number?: string;
  category: string;
  type: string;
  make: string;
  model: string;
}

const MobileInspection: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const [photos, setPhotos] = useState<Photo[]>([]);
  const [currentObject, setCurrentObject] = useState<number | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [location, setLocation] = useState<{ lat: number; lng: number } | null>(null);

  const { data: inspection, isLoading, error } = useQuery({
    queryKey: ['inspection', id],
    queryFn: () => inspectionsApi.getInspection(Number(id)),
    enabled: !!id,
  });

  useEffect(() => {
    //     
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setLocation({
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          });
        },
        (error) => {
          console.error('  :', error);
          toast.error('   ');
        }
      );
    }
  }, []);

  const handleTakePhoto = async (objectId: number) => {
    try {
      //   -  input file
      const input = document.createElement('input');
      input.type = 'file';
      input.accept = 'image/*';
      input.capture = 'environment';
      
      input.onchange = (e: any) => {
        const file = e.target.files[0];
        if (file) {
          const preview = URL.createObjectURL(file);
          
          const photo: Photo = {
            file,
            preview,
            latitude: location?.lat,
            longitude: location?.lng,
            timestamp: new Date().toISOString(),
            objectId,
          };
          
          setPhotos(prev => [...prev, photo]);
          setCurrentObject(null);
        }
      };
      
      input.click();
      
    } catch (error) {
      console.error('   :', error);
      toast.error('     ');
    }
  };

  const handleUploadPhotos = async () => {
    if (photos.length === 0) {
      toast.error('   ');
      return;
    }

    setIsUploading(true);
    try {
      //   -   
      await inspectionsApi.updateStatus(Number(id), '');
      
      toast.success('   ');
      navigate('/inspections');
    } catch (error) {
      toast.error('  ');
    } finally {
      setIsUploading(false);
    }
  };

  const removePhoto = (index: number) => {
    setPhotos(prev => {
      const newPhotos = [...prev];
      URL.revokeObjectURL(newPhotos[index].preview);
      newPhotos.splice(index, 1);
      return newPhotos;
    });
  };

  const getObjectPhotos = (objectId: number) => {
    return photos.filter(photo => photo.objectId === objectId);
  };

  if (isLoading) {
    return (
      <div className="mobile-loading">
        <div className="spinner"></div>
        <p> ...</p>
      </div>
    );
  }

  if (error || !inspection) {
    return (
      <div className="mobile-error">
        <AlertCircle size={48} />
        <h3> </h3>
        <p>    </p>
        <button className="btn btn-primary" onClick={() => navigate('/')}>
           
        </button>
      </div>
    );
  }

  return (
    <div className="mobile-inspection">
      {/*  */}
      <div className="mobile-header">
        <button 
          className="back-btn"
          onClick={() => navigate('/')}
        >
          <ArrowLeft size={24} />
        </button>
        <div className="header-info">
          <h1> #{inspection.data.inspection.id}</h1>
          <p>{inspection.data.inspection.address}</p>
        </div>
        <div className="header-status">
          <span className={`status ${inspection.data.inspection.status.toLowerCase().replace(' ', '-')}`}>
            {inspection.data.inspection.status}
          </span>
        </div>
      </div>

      {/*    */}
      <div className="inspection-info">
        <div className="info-item">
          <MapPin size={16} />
          <span>{inspection.data.inspection.address}</span>
        </div>
        <div className="info-item">
          <Clock size={16} />
          <span>: {new Date(inspection.data.inspection.created_at).toLocaleDateString('ru-RU')}</span>
        </div>
      </div>

      {/*    */}
      <div className="objects-section">
        <h2>  </h2>
        {inspection.data.objects.map((object: InspectionObject) => (
          <div key={object.id} className="object-card">
            <div className="object-header">
              <h3>{object.make} {object.model}</h3>
              <span className="object-category">{object.category}</span>
            </div>
            
            {object.registration_number && (
              <p className="object-reg">. : {object.registration_number}</p>
            )}
            
            {object.vin && (
              <p className="object-vin">VIN: {object.vin}</p>
            )}

            {/*   */}
            <div className="object-photos">
              <h4> ({getObjectPhotos(object.id).length})</h4>
              <div className="photos-grid">
                {getObjectPhotos(object.id).map((photo, index) => (
                  <div key={index} className="photo-item">
                    <img src={photo.preview} alt={` ${index + 1}`} />
                    <button 
                      className="remove-photo"
                      onClick={() => removePhoto(photos.indexOf(photo))}
                    >
                      <X size={16} />
                    </button>
                  </div>
                ))}
                <button 
                  className="add-photo-btn"
                  onClick={() => setCurrentObject(object.id)}
                >
                  <Camera size={24} />
                  <span> </span>
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/*   */}
      <div className="submit-section">
        <button 
          className="btn btn-primary btn-lg submit-btn"
          onClick={handleUploadPhotos}
          disabled={photos.length === 0 || isUploading}
        >
          {isUploading ? (
            <>
              <div className="spinner"></div>
              ...
            </>
          ) : (
            <>
              <Upload size={20} />
                ({photos.length} )
            </>
          )}
        </button>
      </div>

      {/*     */}
      {currentObject && (
        <div className="camera-modal">
          <div className="camera-content">
            <h3> </h3>
            <p>: {inspection.data.objects.find((obj: any) => obj.id === currentObject)?.make} {inspection.data.objects.find((obj: any) => obj.id === currentObject)?.model}</p>
            
            <div className="camera-actions">
              <button 
                className="btn btn-secondary"
                onClick={() => setCurrentObject(null)}
              >
                
              </button>
              <button 
                className="btn btn-primary"
                onClick={() => handleTakePhoto(currentObject)}
              >
                <Camera size={20} />
                 
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default MobileInspection;
