import React, { useState } from 'react';
import { useForm, useFieldArray } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { useNavigate } from 'react-router-dom';
import { 
  Car, 
  Building, 
  Home, 
  Truck, 
  Plus, 
  Trash2, 
  Upload,
  MapPin,
  User,
  Phone,
  Mail,
  FileText
} from 'lucide-react';
import { inspectionsApi } from '../services/api';
import toast from 'react-hot-toast';
import './CreateInspection.css';

const schema = yup.object({
  propertyType: yup.string().required('  '),
  address: yup.string().required(' '),
  latitude: yup.number().optional(),
  longitude: yup.number().optional(),
  inspectorName: yup.string().required('  '),
  inspectorPhone: yup.string().matches(/^\+7\d{10}$/, '  ').required(' '),
  inspectorEmail: yup.string().email('  email').optional(),
  internalNumber: yup.string().optional(),
  comment: yup.string().optional(),
  objects: yup.array().of(
    yup.object({
      vin: yup.string().optional(),
      registrationNumber: yup.string().optional(),
      category: yup.string().required(' '),
      type: yup.string().required(' '),
      make: yup.string().required(' '),
      model: yup.string().required(' '),
    })
  ).min(1, '    '),
});

interface InspectionFormData {
  propertyType: string;
  address: string;
  latitude?: number;
  longitude?: number;
  inspectorName: string;
  inspectorPhone: string;
  inspectorEmail?: string;
  internalNumber?: string;
  comment?: string;
  objects: Array<{
    vin?: string;
    registrationNumber?: string;
    category: string;
    type: string;
    make: string;
    model: string;
  }>;
}

const propertyTypes = [
  { id: 'auto', name: '', icon: Car, color: '#1976D2' },
  { id: 'commercial', name: ' ', icon: Building, color: '#388E3C' },
  { id: 'residential', name: ' ', icon: Home, color: '#F57C00' },
  { id: 'other', name: ' ', icon: Truck, color: '#7B1FA2' },
];

const vehicleCategories = [
  ' ',
  ' ',
  '',
  '',
  '',
  ''
];

const vehicleTypes = [
  '',
  '',
  '',
  '',
  '',
  '',
  '',
  ''
];

const CreateInspection: React.FC = () => {
  const [step, setStep] = useState(1);
  const [isLoading, setIsLoading] = useState(false);
  const navigate = useNavigate();

  const { register, control, handleSubmit, watch, setValue, formState: { errors } } = useForm({
    resolver: yupResolver(schema),
    defaultValues: {
      objects: [{ category: '', type: '', make: '', model: '', vin: '', registrationNumber: '' }]
    }
  });

  const { fields, append, remove } = useFieldArray({
    control,
    name: 'objects'
  });

  const selectedPropertyType = watch('propertyType');

  const onSubmit = async (data: any) => {
    setIsLoading(true);
    try {
      await inspectionsApi.createInspection(data);
      toast.success('  ');
      navigate('/inspections');
    } catch (error: any) {
      toast.error(error.response?.data?.message || '  ');
    } finally {
      setIsLoading(false);
    }
  };

  const handleImportFromExcel = () => {
    //      Excel
    toast('   ');
  };


  return (
    <div className="create-inspection-page">
      <div className="page-header">
        <h1 className="page-title"> </h1>
        <button 
          className="btn btn-secondary"
          onClick={() => navigate('/inspections')}
        >
          
        </button>
      </div>

      <form onSubmit={handleSubmit(onSubmit)} className="inspection-form">
        {/*  1:    */}
        {step === 1 && (
          <div className="step-content">
            <div className="step-header">
              <h2>  </h2>
              <p>    </p>
            </div>

            <div className="property-types">
              {propertyTypes.map((type) => {
                const IconComponent = type.icon;
                return (
                  <button
                    key={type.id}
                    type="button"
                    className={`property-type-card ${selectedPropertyType === type.id ? 'selected' : ''}`}
                    onClick={() => setValue('propertyType', type.id)}
                  >
                    <div className="property-type-icon" style={{ backgroundColor: type.color }}>
                      <IconComponent size={32} />
                    </div>
                    <span className="property-type-name">{type.name}</span>
                  </button>
                );
              })}
            </div>

            {errors.propertyType && (
              <div className="form-error">{errors.propertyType.message}</div>
            )}

            <div className="step-actions">
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => setStep(2)}
                disabled={!selectedPropertyType}
              >
                
              </button>
            </div>
          </div>
        )}

        {/*  2:   */}
        {step === 2 && (
          <div className="step-content">
            <div className="step-header">
              <h2>  </h2>
              <p>    </p>
            </div>

            <div className="form-grid">
              <div className="form-group">
                <label className="form-label required">
                  <MapPin size={16} />
                  
                </label>
                <input
                  type="text"
                  className="form-input"
                  {...register('address')}
                  placeholder="     "
                />
                {errors.address && (
                  <div className="form-error">{errors.address.message}</div>
                )}
              </div>

              <div className="form-group">
                <label className="form-label">
                  <FileText size={16} />
                   
                </label>
                <input
                  type="text"
                  className="form-input"
                  {...register('internalNumber')}
                  placeholder="  "
                />
              </div>

              <div className="form-group">
                <label className="form-label required">
                  <User size={16} />
                   
                </label>
                <input
                  type="text"
                  className="form-input"
                  {...register('inspectorName')}
                  placeholder="  "
                />
                {errors.inspectorName && (
                  <div className="form-error">{errors.inspectorName.message}</div>
                )}
              </div>

              <div className="form-group">
                <label className="form-label required">
                  <Phone size={16} />
                   
                </label>
                <input
                  type="tel"
                  className="form-input"
                  {...register('inspectorPhone')}
                  placeholder="+7 (999) 123-45-67"
                />
                {errors.inspectorPhone && (
                  <div className="form-error">{errors.inspectorPhone.message}</div>
                )}
              </div>

              <div className="form-group">
                <label className="form-label">
                  <Mail size={16} />
                  Email 
                </label>
                <input
                  type="email"
                  className="form-input"
                  {...register('inspectorEmail')}
                  placeholder="email@example.com"
                />
                {errors.inspectorEmail && (
                  <div className="form-error">{errors.inspectorEmail.message}</div>
                )}
              </div>

              <div className="form-group full-width">
                <label className="form-label">
                  
                </label>
                <textarea
                  className="form-input"
                  rows={3}
                  {...register('comment')}
                  placeholder="   "
                />
              </div>
            </div>

            <div className="step-actions">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={() => setStep(1)}
              >
                
              </button>
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => setStep(3)}
              >
                
              </button>
            </div>
          </div>
        )}

        {/*  3:   */}
        {step === 3 && (
          <div className="step-content">
            <div className="step-header">
              <h2> </h2>
              <p>   </p>
            </div>

            <div className="objects-section">
              <div className="objects-header">
                <h3> </h3>
                <div className="objects-actions">
                  <button
                    type="button"
                    className="btn btn-secondary btn-sm"
                    onClick={handleImportFromExcel}
                  >
                    <Upload size={16} />
                      Excel
                  </button>
                  <button
                    type="button"
                    className="btn btn-primary btn-sm"
                    onClick={() => append({ category: '', type: '', make: '', model: '', vin: '', registrationNumber: '' })}
                  >
                    <Plus size={16} />
                     
                  </button>
                </div>
              </div>

              <div className="objects-table">
                <div className="table-header">
                  <div className="table-cell">VIN</div>
                  <div className="table-cell">. </div>
                  <div className="table-cell"></div>
                  <div className="table-cell"></div>
                  <div className="table-cell"></div>
                  <div className="table-cell"></div>
                  <div className="table-cell"></div>
                </div>

                {fields.map((field, index) => (
                  <div key={field.id} className="table-row">
                    <div className="table-cell">
                      <input
                        type="text"
                        className="form-input"
                        {...register(`objects.${index}.vin`)}
                        placeholder="VIN "
                      />
                    </div>
                    <div className="table-cell">
                      <input
                        type="text"
                        className="form-input"
                        {...register(`objects.${index}.registrationNumber`)}
                        placeholder="12377"
                      />
                    </div>
                    <div className="table-cell">
                      <select
                        className="form-select"
                        {...register(`objects.${index}.category`)}
                      >
                        <option value=""></option>
                        {vehicleCategories.map(category => (
                          <option key={category} value={category}>{category}</option>
                        ))}
                      </select>
                    </div>
                    <div className="table-cell">
                      <select
                        className="form-select"
                        {...register(`objects.${index}.type`)}
                      >
                        <option value=""></option>
                        {vehicleTypes.map(type => (
                          <option key={type} value={type}>{type}</option>
                        ))}
                      </select>
                    </div>
                    <div className="table-cell">
                      <input
                        type="text"
                        className="form-input"
                        {...register(`objects.${index}.make`)}
                        placeholder=""
                      />
                    </div>
                    <div className="table-cell">
                      <input
                        type="text"
                        className="form-input"
                        {...register(`objects.${index}.model`)}
                        placeholder=""
                      />
                    </div>
                    <div className="table-cell">
                      <button
                        type="button"
                        className="btn btn-error btn-sm"
                        onClick={() => remove(index)}
                        disabled={fields.length === 1}
                      >
                        <Trash2 size={14} />
                      </button>
                    </div>
                  </div>
                ))}
              </div>

              {errors.objects && (
                <div className="form-error">{errors.objects.message}</div>
              )}
            </div>

            <div className="step-actions">
              <button
                type="button"
                className="btn btn-secondary"
                onClick={() => setStep(2)}
              >
                
              </button>
              <button
                type="submit"
                className="btn btn-primary"
                disabled={isLoading}
              >
                {isLoading ? '...' : ' '}
              </button>
            </div>
          </div>
        )}
      </form>
    </div>
  );
};

export default CreateInspection;
